/* blue - color: #2C5AB4; */
/* gray - color: #5F5F5F */
body {
  margin: 0;
  padding:0;
  font-family: 'Inter', sans-serif;

  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white !important;
  overflow-y: scroll;
}


.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8f8f8;
  padding: 10px 20px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.1);
}
 
nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

nav li {
  margin-right: 20px;
}

nav a {
  text-decoration: none;
  color: #333;
}

.round-button {
  display: inline-block;
  border-radius: 30%;
  background-color: #2C5AB4;
  color: white;
  padding: 10px;
  text-align: center;
  cursor: pointer;
  border: none;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.1);
}

.round-button:hover {
  background-color: #555;
} 
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  padding: 30px;
  width: 80%;
  margin: auto;

}

.round-button {
  display: inline-block;
  padding: 10px;
  border-radius: 15%;
  background-color: #007bff;
  color: #fff;
  border: none;

  cursor: pointer;

  text-align: center;
  text-decoration: none;
  transition: transform 0.2s ease-in-out;
}

.round-button:hover {
  transform: scale(1.1);
}


.logo-container {
  display: flex;
  align-items: center;
}

.logo {
  height: 50px;
  margin-right: 10px;
}

.header-title {
  font-size: 24px;
  margin: 0;
}

.navbar ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.navbar li {
  margin-right: 20px;
}

.navbar a {
  color: #333;
  text-decoration: none;
  font-size: 18px;
}


/*content*/
/* greeting */
.greeting-container {
  width: 50%;
  margin: 0 auto;
}







